interface Todo {
  id: number;
  content: string;
  complete: boolean;
}

type DebouncedFunction = (...args: any[]) => void;
interface DebouncedFunctionWithCancel extends DebouncedFunction {
  cancel: () => void;
}

export function createTodoApp(id: string) {
  let todos: Todo[] = [];
  let Backuptodos: Todo[] = [];
  const root = document.getElementById(id);

  if (!root) {
    throw new Error(`Element with id "${id}" not found`);
  }

  // Get the DOM elements
  const input = root.querySelector('#todo-input') as HTMLInputElement;
  const list = root.querySelector('#todo-list') as HTMLUListElement;
  const info = root.querySelector('#todo-info') as HTMLElement;
  const totalCount = root.querySelector('#total-count') as HTMLElement;
  let filterType: 'all' | 'active' | 'complete' ;
  let dragItem: HTMLElement | null;
  let active = false;
  let currentX: number;
  let currentY: number;
  let initialX: number;
  let initialY: number;
  let xOffset = 0;
  let yOffset = 0;
  let previewTimeout: NodeJS.Timeout | null;
  let mirrorItem = document.getElementById('mirror-item') as HTMLElement; // Get your existing mirror item
  let hoverItem: HTMLElement | null = null;
  function addTodo(content: string) {
    const todo: Todo = {
      id: Date.now(),
      content,
      complete: false,
    };
    todos = [todo, ...todos];
    render('all');
  }

  function moveTodo(dragItemId: string, targetId: string) {
    const dragIndex = todos.findIndex((todo) => todo.id === Number(dragItemId));
    const targetIndex = todos.findIndex((todo) => todo.id === Number(targetId));

    if (dragIndex > -1 && targetIndex > -1) {
      const itemToMove = todos[dragIndex];
      todos.splice(dragIndex, 1);
      todos.splice(targetIndex, 0, itemToMove);
      sortTodos();

      const dragItemElement = list.querySelector(`[data-id="${dragItemId}"]`) as HTMLElement;
        if (dragItemElement) {
            dragItemElement.style.opacity = '0.5';
        }
    }
  }

  function deleteTodo(id: number) {
    todos = todos.filter((todo) => todo.id !== id);
    render('all');
  }

  function sortTodos() {
    todos.sort((a, b) => (a.complete === b.complete ? 0 : a.complete ? 1 : -1));
    render(filterType);
  }

  function toggleTodo(id: number) {
    todos = todos.map((todo) => (todo.id === id ? { ...todo, complete: !todo.complete } : todo));
    sortTodos();
  }
  function render(filter: 'all' | 'active' | 'complete') {
    // Clear the current list
    list.innerHTML = '';

    // Create and append a new li element for each todo
    todos.forEach((todo) => {
      const li = document.createElement('li');
      li.dataset.id = String(todo.id);

      const checkbox = document.createElement('input');
      checkbox.type = 'checkbox';
      checkbox.checked = todo.complete;
      checkbox.hidden = true;
      // checkbox.hidden = true; // This makes the checkbox hidden
      li.appendChild(checkbox);

      // Text node for the content
      const text = document.createTextNode(todo.content);
      li.appendChild(text);

      // Button for deletion
      const deleteButton = document.createElement('button');
      deleteButton.textContent = 'Delete';
      li.appendChild(deleteButton);

      // Styling for completed items
      if (todo.complete) {
        li.style.textDecoration = 'line-through';
        li.style.color = 'gray';
      }
      li.hidden =
        filter === 'all'
          ? false
          : filter === 'complete' && todo.complete
          ? false
          : filter === 'complete'
          ? true
          : filter === 'active' && todo.complete
          ? true
          : false;

      // Append the li element to the list
      list.appendChild(li);
    });

    // Update total count
    totalCount.textContent = String(todos.length);
  }

  function initRender() {
    console.log('inputis', input);
  }

  // Set up event handlers
  input.addEventListener('keypress', (event) => {
    if (event.key === 'Enter') {
      addTodo(input.value);
      input.value = '';
    }
  });

  list.addEventListener('click', (event) => {
    event.stopPropagation();
    const target = event.target as HTMLElement;
    console.log('click', target.tagName);
    if (target.tagName === 'LI') {
      toggleTodo(Number(target.dataset.id));
    } else if (target.tagName === 'BUTTON') {
      deleteTodo(Number(target.parentElement?.dataset.id));
    }
  });
  // Event listeners
  document.addEventListener('mousedown', dragStart, false);
  document.addEventListener('mouseup', dragEnd, false);
  document.addEventListener('mousemove', drag, false);
  document.addEventListener('keydown', (e: KeyboardEvent) => {
    if (e.key === 'Escape') {
      // If ESC is pressed, restore the todos from the backup
      todos = Backuptodos;
      render(filterType);
      
      // You may want to also hide the preview and reset any other changes
      if (mirrorItem) mirrorItem.style.display = 'none';
    }
  });
  info.addEventListener('click', (event) => {
    const target = event.target as HTMLElement;
    let filter: 'all' | 'active' | 'complete' = 'all';
    if (target.tagName === 'BUTTON') {
      if (target.id === 'delete-complete') {
        Backuptodos = todos = todos.filter((todo) => !todo.complete);
      } else {
        filter = target.id.split('-')[1] as 'all' | 'active' | 'complete';
      }
      console.log(todos);
      filterType = filter;
      render(filter);
    }
  });

  function dragStart(e: MouseEvent): void {
    const target = e.target as HTMLElement;
    const checkbox = target.querySelector('input[type="checkbox"]') as HTMLInputElement;
  
    if (target.nodeName === 'LI' && checkbox && !checkbox.checked) {
        Backuptodos = [...todos];
      console.log('dragStart', currentX, xOffset);
      // Replace the class with the class of your todo items
      initialX = e.clientX - xOffset;
      initialY = e.clientY - yOffset;
      dragItem = target;
      target.style.opacity = '0.5';
      if (target) {
        active = true;

        // Set mirror item content and styles to match the target
        mirrorItem.innerHTML = target.innerHTML;
        mirrorItem.style.position = 'absolute';
        mirrorItem.style.opacity = '0.5';
        mirrorItem.style.pointerEvents = 'none';
        mirrorItem.style.zIndex = '1000';

        // Position mirror item at the mouse event position
        mirrorItem.style.left = `${e.clientX}px`;
        mirrorItem.style.top = `${e.clientY}px`;

        // Show the mirror item
        mirrorItem.style.display = 'block';
      }
    }
  }

  function dragEnd(e: MouseEvent): void {
    initialX = currentX;
    initialY = currentY;
    active = false;
    const target = e.target as HTMLElement;
    if (dragItem) {
      dragItem.style.opacity = '1'; // remove blur effect
      let dragItemId = getDataId(dragItem);
      let targetId = getDataId(e.target as HTMLElement);
      console.log('dragEnd', targetId,Backuptodos);
      if (targetId && dragItemId !== targetId) moveTodo(dragItemId, targetId);
      else if(targetId === null){
        todos = Backuptodos;
        render(filterType);
      }
    }
    if (previewTimeout) {
      clearTimeout(previewTimeout);
      previewTimeout = null;
    }
    xOffset = initialX = currentX = 0;
    yOffset = initialY = currentY = 0;
    // Hide the mirror item
    mirrorItem.style.display = 'none';
    target.style.opacity = '1'; 
    
  }

  function drag(e: MouseEvent): void {
    if (active) {
      //console.log('dragging',e.target)
      e.preventDefault();

      currentX = e.clientX - initialX;
      currentY = e.clientY - initialY;

      xOffset = currentX;
      yOffset = currentY;

      if (mirrorItem) {
        setTranslate(currentX, currentY, mirrorItem);

        // 2-second hover effect
        if (previewTimeout) {
          clearTimeout(previewTimeout);
        }

        previewTimeout = setTimeout(() => {
          mirrorItem!.style.opacity = '0.5'; // add blur effect
        }, 2000);
      }
      let elementUnderCursor = document.elementFromPoint(e.clientX, e.clientY);

      if (elementUnderCursor?.nodeName === 'LI' && elementUnderCursor !== hoverItem) {
        if (hoverItem) {
          hoverItem.style.backgroundColor = ''; // reset the styles of the previous hover item
        }
        hoverItem = elementUnderCursor as HTMLElement;
        hoverItem.style.backgroundColor = 'lightgrey'; // update the styles of the new hover item
        if (dragItem) {
          showPreview(dragItem,hoverItem);
        }

        restoreHoverItemStyle();
      } else if (!elementUnderCursor || elementUnderCursor.nodeName !== 'LI') {
        if (hoverItem) {
          hoverItem.style.backgroundColor = ''; // reset the styles of the previous hover item
          hoverItem = null;
          showPreview.cancel();
        }
      }
    }
  }

  function setTranslate(xPos: number, yPos: number, el: HTMLElement): void {
    el.style.transform = `translate3d(${xPos}px, ${yPos}px, 0)`;
  }
  let restoreHoverItemStyle = debounce(() => {
    if (hoverItem) {
      hoverItem.style.backgroundColor = ''; // reset the styles of the previous hover item
      hoverItem = null;
    }
  }, 500); // delay of 500ms
  function debounce(func: Function, wait: number): DebouncedFunctionWithCancel {
    let timeout: ReturnType<typeof setTimeout> | null;

    const functionToExecute: DebouncedFunctionWithCancel = ((...args: any[]) => {
      const later = () => {
        clearTimeout(timeout!);
        func(...args);
      };
      clearTimeout(timeout!);
      timeout = setTimeout(later, wait);
    }) as DebouncedFunctionWithCancel;

    functionToExecute.cancel = function () {
      clearTimeout(timeout!);
    };

    return functionToExecute;
  }

  let showPreview = debounce((dragItem: HTMLElement | null, hoverItem: HTMLElement | null) => {
    let dragItemId = getDataId(dragItem);
    let targetId = getDataId(hoverItem);
    if (dragItemId !== targetId) moveTodo(dragItemId, targetId);
    }, 2000);
  function getDataId(target: HTMLElement): string | null {
    return target.getAttribute('data-id');
  }

  return {
    addTodo,
    toggleTodo,
    deleteTodo,
    initRender,
    moveTodo,
  };
}

// Usage
