    width: 400px;
    margin: 0 auto;
    font-family: Arial, sans-serif;
  }
  
  #todo-input {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    margin-bottom: 10px;
    border:1px solid;
  }
  
  #all-list {
    list-style: none;
    padding: 0;
  }
  
  #all-list li {
    padding: 10px;
    border: 1px solid #ccc;
    margin-bottom: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    opacity: 1;
    transition: opacity 0.3s;
  }
  
  #all-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
  }
  
  #all-info button {
    padding: 5px 10px;
  }
  
